import React from "react";
import betterKubernetes from '../images/better-kubernetes.svg';

export default function Feature1() {
  return (
    <div id="features" className="basic-3">
      <div className="container">
        <div className="row">
          <div className="col-lg-6">
            <img
              className="img-fluid"
              src={betterKubernetes}
              alt="alternative"
            />
          </div>
          <div className="col-lg-6">
            <div className="text-container">
              <h3>The Glasswall Desktop Tool</h3>
              <p>
              Signature-based security seeks to identify and remove dangerous files. 
              Malware morphs and spreads at such a rate that malicious files are often 
              missed and harmless files held, impacting both safety and productivity. 
              Without relying on signatures or detections, Glasswall's 
              products and solutions regenerate clean, safe and visually 
              identical files in milliseconds, securing your organisation without compromise.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
