import React from "react";

export default function Statistics() {
  return (
    <div className="counter">
      <div className="container">
        <div className="row">
          <div className="col-lg-12">
            {/* <!-- Counter --> */}
            <div id="counter">
              <div className="cell">
                <div
                  id="happy-users-count"
                  className="counter-value number-count"
                  // data-count="05"
                >
                  05
                </div>
                <p className="counter-info">Happy Users</p>
              </div>
              <div className="cell">
                <div
                  id="stargazers-count"
                  className="counter-value number-count"
                  // data-count="5"
                >
                  05
                </div>
                <p className="counter-info">
                  <a href="https://github.com/k8-proxy/p-poc-electron-gatsby-website">Stargazers</a>
                </p>
              </div>
              <div className="cell">
                <div
                  id="forks-count"
                  className="counter-value number-count"
                  // data-count="5"
                >
                  05
                </div>
                <p className="counter-info">
                  <a href="https://github.com/k8-proxy/p-poc-electron-gatsby-website">Forks</a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
