import React from "react";
import headerLens from '../images/header-lens.png';

export default function Download() {
  return (
    <div id="download" className="basic-4">
      <div className="container">
        <div className="row">
          <div className="col-lg-6 col-xl-5">
            <div className="text-container">
              <h2>
                Download <span className="blue">Glaswall Desktop Tool</span>
              </h2>
              <p className="p-large">
                Glasswall Desktop is the only Tool, You can check your file content via our rebuild engine.
                Signature-based security seeks to identify and remove dangerous files. Malware morphs and spreads at such a rate that malicious files are often missed and harmless files held, impacting both safety and productivity. Without relying on signatures or detections, Glasswall's products and solutions regenerate clean, safe and visually identical files in milliseconds, securing your organisation without compromise.
              </p>
              <a
                className="btn-solid-lg"
                href="https://github.com/k8-proxy/k8-electron-react/releases"
              >
                <i className="fas fa-download"></i> Download
              </a>
              <i className="supported-os-icon fab fa-apple"></i>
              <i className="supported-os-icon fab fa-windows"></i>
              <i className="supported-os-icon fab fa-linux"></i>
            </div>
          </div>
          <div className="col-lg-6 col-xl-7">
            <div className="image-container">
              <img
                className="img-fluid"
                src={headerLens}
                alt="alternative"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
